.camera_div{
    max-width: 100%;
    max-height: 100%;
}

#webcam{
    
}

.main_top_box{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.top_alert_box{
    height: 80px;
    width: 95%;
    background-color: rgba(211, 211, 211, 0.3);
    margin-top: 10px;
    position: absolute;
    top: 0;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: left;
    border: 1px solid rgba(211, 211, 211, 0.7);
}

.message_box{
    font-size: 20px;
    color: rgb(86, 86, 86);
    margin-left: 15px;
}


/* bootom box */

.bottom_info_box{
    height: 300px;
    width: 95%;
    background-color: rgba(124, 220, 255, 0.2);
    margin-top: 10px;
    position: absolute;
    bottom: 0;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgba(124, 220, 255, 0.7);
    row-gap: 10px;
    column-gap: 10px;
    padding: 0 10px;
}

.side_bottom1{
    width: 30%;
    height: 90%;
    border-radius: 10px;
   
}

 h4{
    color: white;
    font-weight: 400;
    margin: 0;
    margin-bottom: 5px;
}

.side_bottom2{
    width: 30%;
    height: 90%;
    border-radius: 10px;
   
}

.side_bottom3{
    width: 60%;
